import { Lightning, Utils, Router, Img } from '@lightningjs/sdk'
import { Button, ButtonSmall, Column } from '@lightningjs/ui-components'

export class PosterComponent extends Lightning.Component {
    static _template() {
        return {
            rect: true,
            color: 0xFF13885b,
            w: 500,
            h: 350,
            x: 0,
            y: 0,
            Poster: {
                assetId: 0,
                w: 500,
                h: 250,
                x: 250,
                y: 125,
                mountX: 0.5,
                mountY: 0.5,
                color: 0xFF858585,
                texture: {
                    resizeMode: {
                        type: 'contain',
                        w: 500,
                        h: 250,
                        clipY: 0
                    },
                    type: lng.textures.ImageTexture,
                    src: "https://image.tmdb.org/t/p/w220_and_h330_face/sERwJxz0sqsbcUoTm66l9pI6HcH.jpg"
                }
            },
            PosterTitle: {
                y: 280,
                x: 0,
                text: {
                    w: 500,
                    text: "Poster title2Poster title2Poster title2Poster title",
                    fontSize: 24,
                    fontFace: 'Bold',
                    maxLines: 2,
                    textAlign: 'center'
                }
            },
            PosterReleaseDate: {
                y: 255,
                x: 0,
                text: {
                    w: 500,
                    text: "2023-12-14",
                    fontSize: 22,
                    fontFace: 'Bold',
                    textAlign: 'center'
                }
            }
        }
    }

    _init() {
    }

    _handleEnter() {
        Router.navigate(`movie/details/${this.tag("Poster").assetId}`)
        console.log(this.tag("Poster").assetId)
    }

    pageTransition(){
        return "fade";
    }

    _focus(){
        this.patch({
            color: 0xFF24BD7E
        })
        this.tag("Poster").patch({
            color: 0xFFFFFFFF
        })
    }

    _unfocus(){
        this.patch({
            color: 0xFF13885b
        })
        this.tag("Poster").patch({
            color: 0xFF858585
        })
    }

    set posterID(id) {
        this.tag("Poster").patch({ assetId: id })
    }

    set posterUrl(url) {
        this.tag("Poster").patch({ src: url })
    }

    set posterTitle(title) {
        this.tag("PosterTitle").patch({
            text: {
                text: title
            }
        })
    }

    set posterReleaseDate(releaseDate) {
        this.tag("PosterReleaseDate.text").patch({ text: releaseDate })

    }

    set posterPosition(idx) {
        this.patch({ x: idx })
    }
}
