import { Lightning, Router, Utils, VideoPlayer } from "@lightningjs/sdk";
import { Button, ButtonSmall, Column, Row, Key } from '@lightningjs/ui-components'

import { options } from "../lib/api";

import { HeaderComponent } from "../components/HeaderComponent";

const getAssetDetails = async (assetId) => {
    const response = await fetch(`https://api.themoviedb.org/3/movie/${assetId}?language=en-USs`, options);
    const data = await response.json();

    return data
}

export default class Player extends Lightning.Component {

    static assetID

    static _template() {
        return {
            SkipBBtn: {
                type: Key,
                x: 650,
                y: 900,
                fixed: true,
                size: "sm",
                w: 150,
                h: 150,
                icon: Utils.asset("images/icons_player/back-gray.svg")
            },
            PlayPauseBtn: {
                type: Key,
                x: 850,
                y: 900,
                fixed: true,
                size: "sm",
                w: 150,
                h: 150,
                icon: Utils.asset("images/icons_player/play-gray.svg")
            },
            SkipFBtn: {
                type: Key,
                x: 1050,
                y: 900,
                fixed: true,
                size: "sm",
                w: 150,
                h: 150,
                icon: Utils.asset("images/icons_player/forward-gray.svg")
            },
        }
    }

    _firstActive() {
        const videoUrl = 'http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/BigBuckBunny.mp4'
        VideoPlayer.consumer(this)
        VideoPlayer.show()
        VideoPlayer.open(videoUrl)
        VideoPlayer.play(videoUrl)

        this._setState("PlayPauseBtn")
    }

    // _getFocused() {
    //     return this.tag("PlayPauseBtn")
    // } 

    static _states() {
        return [
            class SkipBBtn extends this {
                _getFocused() {
                    return this.tag("SkipBBtn")
                }

                _handleEnter() {
                    VideoPlayer.skip(-20)
                }

                _handleRight() {
                    this._setState("PlayPauseBtn")
                }

                _focus() {
                    this.tag("SkipBBtn").patch({ icon: Utils.asset("images/icons_player/back-black.svg") })
                }

                _unfocus() {
                    this.tag("SkipBBtn").patch({ icon: Utils.asset("images/icons_player/back-gray.svg") })
                }
            },
            class PlayPauseBtn extends this {
                _getFocused() {
                    return this.tag("PlayPauseBtn")
                }

                _handleLeft() {
                    this._setState("SkipBBtn")
                }

                _handleEnter() {
                    if (VideoPlayer.playing) {
                        this.tag("PlayPauseBtn").patch({ icon: Utils.asset("images/icons_player/play-gray.svg") })
                    } else {
                        this.tag("PlayPauseBtn").patch({ icon: Utils.asset("images/icons_player/pause-gray.svg") })
                    }
                    VideoPlayer.playPause()
                }

                _handleRight() {
                    this._setState("SkipFBtn")
                }

                _focus() {
                    this.tag("PlayPauseBtn").patch({ icon: Utils.asset("images/icons_player/play-gray.svg") })
                }

                _unfocus() {
                    this.tag("PlayPauseBtn").patch({ icon: Utils.asset("images/icons_player/play-gray.svg") })
                }
            },
            class SkipFBtn extends this {
                _getFocused() {
                    return this.tag("SkipFBtn")
                }

                _handleLeft() {
                    this._setState("PlayPauseBtn")
                }

                _handleEnter() {
                    VideoPlayer.skip(20)
                }

                _focus() {
                    this.tag("SkipFBtn").patch({ icon: Utils.asset("images/icons_player/forward-black.svg") })
                }

                _unfocus() {
                    this.tag("SkipFBtn").patch({ icon: Utils.asset("images/icons_player/forward-gray.svg") })
                }
            },
        ]
    }


    _handleBack() {
        VideoPlayer.reload()
        setTimeout(() => {
            VideoPlayer.pause()
        }, 100)
        VideoPlayer.hide()
        Router.navigate(`movie/details/${this.assetID}`)
    }

    pageTransition() {
        return "fade";
    }

    _onUrlParams(args) {
        this.assetID = args.assetId
    }
}