import { Lightning, Router, Utils } from "@lightningjs/sdk";
import { Button, ButtonSmall, Column, Row } from '@lightningjs/ui-components'

import { options } from "../lib/api";

import { HeaderComponent } from "../components/HeaderComponent";

const getAssetDetails = async (assetId) => {
    const response = await fetch(`https://api.themoviedb.org/3/movie/${assetId}?language=en-US`, options);
    const data = await response.json();

    return data
}

export default class Details extends Lightning.Component {
    static _template() {
        return {
            //Foreground Img
            AssetImgBackground: {
                w: 1920,
                h: 800,
                colorTop: 0xFF858585,
                colorBottom: 0xFF858585,
                src: "https://image.tmdb.org/t/p/w1920_and_h800_multi_faces/sERwJxz0sqsbcUoTm66l9pI6HcH.jpg"
            },
            //Rest of Foreground as solid color
            AssetColorBackground: {
                rect: true,
                w: 1920,
                h: 1080,
                y: 400,
                color: 0xFF000000
                // colorTop: 0xff7CB9E8,
                // colorBottom: 0xff2a52be,
            },
            Header: {
                type: HeaderComponent
            },
            //Asset details (title, release date, overview, poster and buttons to play or add favourite)
            AssetTitle: {
                rect: true,
                w: 1920,
                h: 100,
                y: 400,
                color: 0xFF24BD7E,
                ATitle: {
                    y: 25,
                    x: 550,
                    text: {
                        text: "Title",
                        fontFace: 'Bold'
                    }
                }
            },
            AssetButtons: {
                type: Row,
                y: 520,
                x: 550,
                wrapSelected: true,
                items: [
                    {
                        type: ButtonSmall,
                        fixed: true,
                        w: 250,
                        h: 50,
                        title: "Watch Trailer"
                    },
                    {
                        type: ButtonSmall,
                        fixed: true,
                        w: 300,
                        h: 50,
                        x: 100,
                        title: "Add Favourite"
                    }
                ]
            },
            AssetReleaseDate: {
                y: 590,
                x: 550,
                text: {
                    text: "2023-12-14",
                    fontFace: 'Bold',
                    fontSize: 24
                }
            },
            AssetOverview: {
                y: 625,
                x: 550,
                text: {
                    w: 600,
                    text: "Lorem Ipsum",
                    fontFace: 'Bold',
                    fontSize: 24,
                    maxLines: 10
                }
            },
            AssetPoster: {
                w: 320,
                h: 430,
                y: 230,
                x: 200,
                src: "https://image.tmdb.org/t/p/w500/sERwJxz0sqsbcUoTm66l9pI6HcH.jpg"
            }
        }
    }

    _getFocused() {
        return this.tag("AssetButtons")
    }

    _init() {
    }

    _handleBack() {
        Router.navigate(`home`)
    }

    _handleEnter() {
        Router.navigate(`movie/details/play/${this.tag("AssetPoster").assetId}`)
    }

    pageTransition() {
        return "fade";
    }

    _onUrlParams(args) {
        const assetResult = new Promise(async (resolve, reject) => {
            try {
                const result = await getAssetDetails(args.assetId);
                resolve(result);
            } catch (error) {
                reject(error);
            }
        });

        assetResult.then((assetData) => {
            this.tag("AssetImgBackground").patch({ src: `https://image.tmdb.org/t/p/w1920_and_h800_multi_faces${assetData.backdrop_path}` })
            this.tag("AssetTitle.ATitle").patch({ text: { text: assetData.title } })
            this.tag("AssetReleaseDate").patch({ text: { text: assetData.release_date } })
            this.tag("AssetOverview").patch({ text: { text: assetData.overview } })
            this.tag("AssetPoster").patch({ src: `https://image.tmdb.org/t/p/w500/${assetData.poster_path}` })
            this.tag("AssetPoster").patch({ assetId: assetData.id })
        }).catch((error) => {
            console.error("Error fetching asset details:", error);
        });
    }
}