import { Lightning, Router, Utils } from "@lightningjs/sdk";
import { Lifecycle, Metrics, Device } from '@firebolt-js/sdk'
import { Button, Column, Row } from '@lightningjs/ui-components'

import { options } from "../lib/api";

import { BackgroundComponent } from "../components/BackgroundComponent";
import { HeaderComponent } from "../components/HeaderComponent";
import { MenuComponent } from "../components/MenuComponent";
import { PosterComponent } from "../components/PosterComponent";

// Methods
const createItems = async (element, pageToLook) => {
    const response = await fetch(`https://api.themoviedb.org/3/discover/movie?include_adult=false&include_video=false&language=en-US&page=${pageToLook}&sort_by=popularity.desc`, options);
    const data = await response.json();

    switch (element) {
        case 'cardContentV':
            try {
                const response = await fetch(`https://api.themoviedb.org/3/discover/movie?include_adult=false&include_video=false&language=en-US&page=${pageToLook}&sort_by=popularity.desc`, options);
                const data = await response.json();

                const cards = data.results.map((poster, idx) => ({
                    type: PosterComponent,
                    posterUrl: `https://image.tmdb.org/t/p/w1280_and_h720_multi_faces${poster.backdrop_path}`,
                    posterTitle: poster.title,
                    posterDesc: poster.overview,
                    posterID: poster.id,
                    y: 165,
                }));

                return {
                    items: cards,
                };
            } catch (err) {
                console.error(err);
                return { items: [] };
            }

        default:
            break;
    }

};

export default class Home extends Lightning.Component {

    static _template() {
        return {
            //Background
            Background: {
                type: BackgroundComponent
            },
            //SuperContainer
            ContainerInstance: {
                type: Column,
                h: 1080, w: 1920,
                y: 300, x: 350,
                alwaysScroll: true,
                wrapSelected: true,
                items: [
                    {
                        type: Row,
                        h: 350,
                        items: [],
                        alwaysScroll: true,
                    },
                    {
                        type: Row,
                        h: 350,
                        items: [],
                        alwaysScroll: true,
                    },
                    {
                        type: Row,
                        h: 350,
                        items: [],
                        alwaysScroll: true,
                    },
                    {
                        type: Row,
                        h: 350,
                        items: [],
                        alwaysScroll: true,
                    },
                ]
            },
            //Black box wrapping the header and text, also ensures that when the row goes up does the appareance that hides
            TextWrapper: {
                rect: true,
                w: 1920,
                h: 300,
                x: 0,
                color: 0xFF000000
            },
            //Header
            Header: {
                type: HeaderComponent
            },
            WelcomeText: {
                x: 350, y: 120,
                text: {
                    fontSize: 24,
                    text: "Welcome, user",
                    color: 0xff0000,
                    fontFace: 'Bold'
                }
            },
            DeviceSpecs: {
                x: 350, y: 160,
                ResolutionText: {
                    y: 0,
                    text: {
                        fontSize: 24,
                        text: "Screen Resolution: ",
                        color: 0xff3C57B9
                    }
                },
                DevNameText: {
                    x: 200, y: -40,
                    text: {
                        fontSize: 24,
                        text: "Current device: ",
                        color: 0xff3C57B9
                    }
                },
                VersionsText: {
                    x: 350, y: 0,
                    text: {
                        fontSize: 24,
                        text: "OS Version: ",
                        color: 0xff3C57B9
                    }
                },
                ConnectedText: {
                    x: 0, y: 40,
                    text: {
                        fontSize: 24,
                        text: "Network status: ",
                        color: 0xff3C57B9
                    }
                },
            },
            //Menu Container
            MenuInstance: {
                type: MenuComponent
            },
        }
    }

    _init() {
        Lifecycle.ready()
            .then(result => {
                // console.log(result)
                // console.log("App is ready to go")

                Device.network()
                    .then(async (networkInfo) => {
                        if (networkInfo.state === "connected") {
                            // Do something if connected
                        } else {
                            // Do something if disconnected
                        }

                        this.tag("ConnectedText").patch({
                            text: `Network status: ${networkInfo.state.toUpperCase()}`
                        })
                    })

                Device.screenResolution().then(screenRes => {
                    this.tag("ResolutionText").patch({
                        text: `Screen Resolution: ${screenRes[0]}x${screenRes[1]}`
                    })
                })

                Device.version().then((versions) => {
                    this.tag("VersionsText").patch({
                        text: `OS Version: ${versions.os.readable}`
                    })
                })

                Device.name().then((value) => {
                    this.tag("DevNameText").patch({
                        text: `Current device: ${value}`
                    })
                })
            })

        var rowLength = this.tag("ContainerInstance").items.length

        for (let i = 0; i < rowLength; i++) {
            createItems("cardContentV", (i + 1))
                .then(result => {
                    this.tag("ContainerInstance").items[i].items = result.items;
                })
                .catch(error => {
                    console.error(error);
                });
        }

        super._init();

        this._setState("MenuInstance")
    }

    static _states() {
        return [
            class ContainerInstance extends this {
                _getFocused() {
                    return this.tag("ContainerInstance")
                }

                _handleLeft() {
                    this.tag("MenuInstance").patch({ visible: true })
                    this.tag("ContainerInstance").patch({ x: 350 })
                    this.tag("WelcomeText").patch({ x: 350 })
                    this.tag("DeviceSpecs").patch({ x: 350 })
                    this._setState("MenuInstance")
                }
            },

            class MenuInstance extends this {
                _getFocused() {
                    return this.tag("MenuInstance")
                }

                _handleRight() {
                    this._setState("ContainerInstance")
                    this.tag("MenuInstance").patch({ visible: false })
                    this.tag("ContainerInstance").patch({ x: 50 })
                    this.tag("WelcomeText").patch({ x: 50 })
                    this.tag("DeviceSpecs").patch({ x: 50 })
                }
            },
        ]
    }

    pageTransition() {
        return "fade";
    }
}