import {
    Home, Details, Player
} from "../pages"

export default {
    root: 'home',
    routes: [
        {
            path: 'home',
            component: Home,
        },
        {
            path: 'movie/details/:assetId',
            component: Details
        },
        {
            path: 'movie/details/play/:assetId',
            component: Player
        }
    ],
}